body, html {
    height: 100%;
    width: 100%;
    padding: 0;
    margin: 0;
    overflow: hidden;
    color: #333;
    font-size: 12px;
}

ul, li {
    list-style: none;
    margin: 0;
    padding: 0;
}

a {
    color: #fff;
    text-decoration: none;
}
.main {
    height: 100%;
    background-color: #fff;
    font-size: 0;
}

.step {
    transform-origin: -50% -50%;
    font-size: 12px;
    height: 100%;
    width: 100%;
    padding: 0;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    transition: all 1s;
    overflow: auto;
}

.step1 {
    background-color: #22B573;
    line-height: 100%;
}

.step2 {
    background-color: #1889C5;
}

.step3 {
    background-color: #9C58B6;
}

.step4 {
    background-color: #26B1B6;
}

.step5 {
    background-color: #129DE7;
}

@keyframes fallsDown {
    0% {
        opacity: 1;
        transform: rotateZ(0deg) translate(-50%, -50%)
    }
    30% {
        transform: rotateZ(50deg) translate3d(-50%, -50%, 0)
    }
    50% {
        transform: rotateZ(40deg) translate3d(-50%, -50%, 0)
    }
    70%, 80% {
        transform: rotateZ(45deg) translate3d(-50%, -50%, 0)
    }
    83% {
        transform: rotateZ(45deg) translate3d(-40%, -40%, 0)
    }
    85% {
        transform: rotateZ(45deg) translate3d(-30%, -30%, 0)
    }
    88% {
        transform: rotateZ(45deg) translate3d(-20%, -20%, 0)
    }
    90% {
        transform: rotateZ(45deg) translate3d(0, 0, 0)
    }
    100% {
        transform: rotateZ(45deg) translate3d(2000px, 2000px, 0);
        opacity: 0;
    }
}

@keyframes getBackHere {
    100% {
        transform: rotateZ(0deg) translate3d(-50%, -50%, 0);
        opacity: 1
    }
    30% {
        transform: rotateZ(45deg) translate3d(-10%, -10%, 0)
    }
    0% {
        transform: rotateZ(45deg) translate3d(2000px, 2000px, 0);
        opacity: 0
    }
}

.fallsDown {
    animation: fallsDown 1s linear forwards;
}

.getBackHere {
    animation: getBackHere .4s linear forwards;
}

.main {
    position: relative;
}

.next, .prev {
    opacity: .4;
    z-index: 99;
    position: fixed;
    right: 40px;
    top: 14%;
    height: 80px;
    width: 80px;
    cursor: pointer;
    font-size: 80px;
    line-height: 80px;
    text-align: center;
    color: #fff;
}

.prev {
    display: none;
}

.next {
    top: 75%;
}

.step1>div {
    font-size: 100px;
    color: white;
    text-align: center;
    width: 100%;
    transform: translateY(-100%);
    top: 0%;
    position: absolute;
    transition: all 1s;
    opacity: 0;
}

.step1>div>p {
    margin: 0 auto;
    text-align: center;
    width: 100%;
    display: block;
    line-height: 100px;
    ;
}

.step1 .fadeIn {
    top: 50%;
    transform: translateY(-50%);
    opacity: 1;
}

.step2 .container {
    display: flex;
    justify-content: space-between;
    width: 70%;
    margin: 0 auto;
    margin-top: 40px;
    align-items: center;
}

.container-small {
    color: #fff;
    display: flex;
    justify-content: space-between;
    width: 50%;
    margin: 0 auto;
    margin-top: 40px;
    align-items: top;
}

.container-small>div {
    font-size: 16px;
    flex: 1;
    text-align: left;
}

.container-small>div:last-child:first-child {
    margin-left: 40px;
}

.container-small>div:last-child {
    text-align: left;
    flex: 1;
}

.container-small>div>ul>li {
    margin-top: 10px;
    line-height: 30px;
}

.step2 .container>div {
    width: 25%;
    text-align: center;
}

.container .image_box {
    height: auto;
    overflow: hidden;
    /* border-radius: 50%; */
    width: 22%;
}

.container .image_box img {
    width: 100%;
    height: 100%;
}

.container .item img {
    display: inline-block;
    /* border-radius: 50%; */
    transition: .8s all;
    width: 28%;
}

.container .item a {
    overflow: hidden;
}

.container .item img:hover {
    transform: rotate(360deg)
}
.title{
    font-size: 35px;
    color: #fff;
    text-align: center;
    margin-top: 20px;
}
.step3 > .container{

}
.step3 > .container > .skill{
    width: 60%;
    margin: 0 auto;
    display: flex;
    justify-content:space-around;
    font-size: 16px;
    color: #fff;
    margin-top: 200px;
    flex-flow:row wrap;
    align-items: flex-start;

}
.step3 > .container > .skill ul{
    min-width: 200px;
    width: 20%;
    border: 4px solid #fff;
    box-sizing: border-box;
    margin-bottom: 20px;
    margin-right: 20px;
    margin-left: 20px;
    overflow: hidden;
    /* display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center; */
}
.step3 > .container > .skill ul li{
    text-align: center;
    line-height: 35px;
    margin-top: -1px
}

.step3 > .container > .skill ul li:hover{
    background-color: #fff;
    color: #333;
}
.step3 > .container > .skill ul li:first-child{
    background-color: #fff;
    color: #333;
    width: 100%;
    display: block;
    margin-top: 0px;
    cursor: default;
    width: 100%;
}
@media screen and (max-width:1000px) {
    .step {
        border-radius: 0;
        width: 100%;
    }
    .prev, .next {
        right: 14px;
        font-size: 60px;
    }
    .container {
        width: 100%;
    }
    .step1>div>p {
        font-size: 60px;
    }
    .next {
        top: 85%;
    }
  
    .container .item img {
        width: 50%;
    }
    .container-small>div:last-child:first-child {
        margin-left: 0;
    }
    .container-small>div {
        font-size: 10px;
    }
    .container .image_box {
        width: 22%;
    }
    .container .image_box img {
        width: 100%;
    }
    .container-small {
        margin-left: 20%;
        width: 70%;
        margin-top: 20px;
        align-items: top;
    }
    .container-small>div>ul>li{
        line-height: 15px;
    }
}